input{
  height:56 px;
}
.search {
  /* width: 100%; */
  /* position: relative; */
  display: flex;
  
}

.searchTerm {
  height:56 px;
  width: 100%;  
  border: 3px solid ;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  /* color: #9DBFAF; */
}

.searchTerm:focus{
  color: #00B4CC;
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid grey ;
  background: grey;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

